.navbar {
  transition: all 0.4s;
  margin-left: auto;
  margin-right: auto;

  padding-left: 22px;
  padding-right: 22px;
  height: 44px;

  background: #fff;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

.navitem.nav-link:hover,
.navitem.nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}


.navbar .navbar-brand {
  color: #000;
}


/* Change navbar styling on scroll */
.navbar.active {
  background: rgb(255, 255, 255);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

.navbar.active .nav-link {
  color: #66cc33;
}

.navbar.active .nav-link:hover,
.navbar.active .nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}

.navbar.active .navbar-brand {
  color: #555;
}


.nav-link {
  display: block;
  padding: 7px 0px;
  text-align: center;
}
.nav-item:hover {
  color: #66cc33;
}

.nav-link:hover,
.nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}

.hover-item {
	color: #66cc33;
}

.hover-item:hover {
	background-color: inherit;
}



@media screen and (max-width: 991px) {

    .navbar {
      background: #fff;
      height: auto;
    }
  
    .navbar .navbar-brand, .navbar .nav-link {
      color: #555;
    }
  }
  
  