.loader:empty {
  position: absolute;
  top: calc(50% - 1.1em);
  left: calc(50% - 1.1em);
  width: 3em;
  height: 3em;
  border: 0.3em solid rgba(0, 0, 0, 0.2);
  border-left: 0.3em solid  #66cc33;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.center {
  margin: auto;
  width: 50%;
  border: 3px solid green;
  padding: 10px;
}


body { 
    -webkit-animation: fadeInAnimation ease 3s; 
            animation: fadeInAnimation ease 3s; 
    -webkit-animation-iteration-count: 1; 
            animation-iteration-count: 1; 
    -webkit-animation-fill-mode: forwards; 
            animation-fill-mode: forwards; 
} 
@-webkit-keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
    } 
} 
@keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
    } 
} 
  
.navbar {
  transition: all 0.4s;
  margin-left: auto;
  margin-right: auto;

  padding-left: 22px;
  padding-right: 22px;
  height: 44px;

  background: #fff;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

.navitem.nav-link:hover,
.navitem.nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}


.navbar .navbar-brand {
  color: #000;
}


/* Change navbar styling on scroll */
.navbar.active {
  background: rgb(255, 255, 255);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

.navbar.active .nav-link {
  color: #66cc33;
}

.navbar.active .nav-link:hover,
.navbar.active .nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}

.navbar.active .navbar-brand {
  color: #555;
}


.nav-link {
  display: block;
  padding: 7px 0px;
  text-align: center;
}
.nav-item:hover {
  color: #66cc33;
}

.nav-link:hover,
.nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}

.hover-item {
	color: #66cc33;
}

.hover-item:hover {
	background-color: inherit;
}



@media screen and (max-width: 991px) {

    .navbar {
      background: #fff;
      height: auto;
    }
  
    .navbar .navbar-brand, .navbar .nav-link {
      color: #555;
    }
  }
  
  
.left {
    text-align: left;
  }

  .fa {
    padding: 20px;
    font-size: 30px;
    width: 10px;
    text-align: center;
    text-decoration: none;
